import React from 'react';
import "../assets/css/Skills.css";

function Skills() {
  return (
    <div className="skill_section" id="Skills">
      <h2 className="section_a__title">Skills</h2>
      <span className="section_a__subtitle">Experienced</span>

      <div className="skills__grid">
        {/* Frontend */}
        <div className="skills__card frontend">
          <h2 className="skills__card-title">Frontend</h2>
          <ul className="skills__list frontend__icons">
            <li><img src="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/html5/html5-original.svg" alt="HTML5" /></li>
            <li><img src="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/css3/css3-original.svg" alt="CSS3" /></li>
            <li><img src="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/javascript/javascript-original.svg" alt="JavaScript" /></li>
            <li><img src="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/react/react-original.svg" alt="React" /></li>
            <li><img src="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/sass/sass-original.svg" alt="Sass" /></li>
          </ul>
        </div>

        {/* Backend */}
        <div className="skills__card">
          <h2 className="skills__card-title">Backend</h2>
          <ul className="skills__list">
            <li><img src="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/java/java-original.svg" alt="Java" /></li>
            <li><img src="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/spring/spring-original.svg" alt="Spring" /></li>
            <li><img src="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/nodejs/nodejs-original.svg" alt="Node.js" /></li>
          </ul>
        </div>

        {/* Database */}
        <div className="skills__card database">
          <h2 className="skills__card-title">Database</h2>
          <ul className="skills__list big-icons">
            <li><img src="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/oracle/oracle-original.svg" alt="Oracle" /></li>
            <li><img src="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/mysql/mysql-original-wordmark.svg" alt="MySQL" /></li>
          </ul>
        </div>

        {/* Etc */}
        <div className="skills__card etc">
          <h2 className="skills__card-title">Etc</h2>
          <ul className="skills__list big-icons">
            <li><img src="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/git/git-original.svg" alt="Git" /></li>
            <li><img src="https://cdn.jsdelivr.net/gh/devicons/devicon/icons/github/github-original-wordmark.svg" alt="GitHub" /></li>
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Skills;