import React from 'react'
import "../assets/css/Home.css";
import { Link } from 'react-scroll';


function Home() {

  return (
   
          <div className="home_section" id="Home">
              <div className="home__container container grid">
                    <div className="home__content grid">
                        {/* <div className="home__img"></div> */}
                        <div className="home__data">
                              <h1 className="home__title">Developer Portfolio</h1>
                              <h3 className="home__subtitle"></h3>
                              <p className="home__description">   <br/>       </p>
                        </div>                        
                    </div>

                    <div className="home__scroll">
                        <Link to="About" smooth={true} duration={0}>
                            {/* <a href="" className="home_scroll-button"> */}
                                <span className="home__scroll-name">Who am I? </span>
                                <i class="uil uil-arrow-down home__scroll-arrow"></i>
                        </Link>
                    </div>
                    
              </div>
          </div>
          
    
  )
}

export default Home