import React, { useEffect, useMemo, useState } from "react";
import "../assets/css/Header.css";
import { throttle } from "lodash";
import { Link } from "react-scroll";

function Header() {
  const [isWhite, setIsWhite] = useState(true);
  const [menuOpen, setMenuOpen] = useState(false);

  const getTop = (id) => {
    const el = document.getElementById(id);
    return el ? el.offsetTop : 0;
  };

  const getBottom = (id) => {
    const el = document.getElementById(id);
    return el ? el.offsetTop + el.offsetHeight : 0;
  };

  const scrollEvent = useMemo(
    () =>
      throttle(() => {
        const scrollY = window.scrollY;

        const homeTop = getTop("Home");
        const homeBottom = getBottom("Home");

        const experienceTop = getTop("Experience");
        const experienceBottom = getBottom("Experience");

        if (
          (scrollY >= homeTop && scrollY < homeBottom) ||
          (scrollY >= experienceTop && scrollY < experienceBottom)
        ) {
          setIsWhite(true);
        } else {
          setIsWhite(false);
        }
      }, 100),
    []
  );

  useEffect(() => {
    window.addEventListener("scroll", scrollEvent);
    scrollEvent();
    return () => {
      window.removeEventListener("scroll", scrollEvent);
    };
  }, [scrollEvent]);

  //   
  const toggleMenu = () => {
    setMenuOpen(!menuOpen);
    //    /
    if (!menuOpen) {
      document.body.classList.add("menu-open");
    } else {
      document.body.classList.remove("menu-open");
    }
  };

  //    
  const handleLinkClick = () => {
    setMenuOpen(false);
    document.body.classList.remove("menu-open");
  };

  return (
    <div className={isWhite ? "NavContainer white" : "transNavContainer black"}>
      <nav id="nav">
        <div className="logo">
          <Link to="Home" smooth={true} duration={0} onClick={handleLinkClick}>
            MINSEONG
          </Link>
        </div>

        {/*   -   */}
        <div
          className={`hamburger ${menuOpen ? "active" : ""} ${isWhite ? "white" : "black"}`}
          onClick={toggleMenu}
          aria-label="Toggle menu"
        >
          <span></span>
          <span></span>
          <span></span>
        </div>

        {/*  */}
        <ul className={`nav-links ${menuOpen ? "mobile-active" : ""}`}>
          <li>
            <Link to="Home" smooth={true} duration={0} onClick={handleLinkClick}>
              Home
            </Link>
          </li>
          <li>
            <Link to="About" smooth={true} duration={0} onClick={handleLinkClick}>
              About
            </Link>
          </li>
          <li>
            <Link to="Experience" smooth={true} duration={0} onClick={handleLinkClick}>
              Experience
            </Link>
          </li>
          <li>
            <Link to="Skills" smooth={true} duration={0} onClick={handleLinkClick}>
              Skills
            </Link>
          </li>
          <li>
            <Link to="Projects" smooth={true} duration={0} onClick={handleLinkClick}>
              Projects
            </Link>
          </li>
        </ul>
      </nav>
    </div>
  );
}

export default Header;
